import { makeObservable, observable, action } from 'mobx';
import * as util from './MassDetailTableUtil'

export class MassDetailTable {
  constructor() {
    // makeObservable(this, {

    // })
  }

  data
  get Data(){
    return this.data;
  } set Data(v){
    this.data = v;
    this.Table.DataSource = v;
  }

  columns
  get Columns(){
    if (!this.columns){
      this.columns = util.GetDefaultColumns(this);
    }
    return this.columns;
  }

  table
  get Table(){
    if (!this.table){
      this.table = util.GetTable(this);
    }
    return this.table;
  }

  AddColumn = () => {
    this.Table.AddColumn();
  }
}