import * as mif from '../item/MassItemField'
import * as tableUtil from '../../../../table/TableVMUtil'
import * as cit from '../../../../table/TableColumn_InputType'

// Property
export const GetDefaultColumns = (q) => {
  return [ mif.Value, mif.Unit ]
  //return [ mif.Value]
}

export const GetTable = (q) => {
  var qI = tableUtil.GetByColumn_Data(q.Columns, [], false);
  qI.CanHandleItem = false;
  qI.AllColumnNames = mif.GetForMassGroup();
  qI.Column_InputType = cit.select
  qI.Override_Get_CellValue = (item, cell, row, col) => {
    var field = col.DataIndex[0];
    var val = item.GetValue(field);
    return val;
  }
  qI.Column_Title_SelectWidth = 150
  return qI;
}