import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Col, Row } from 'antd';
import { withRouter } from "react-router-dom";
import { MassDetailVM } from './MassDetailVM';
import MassDetailTableView from './MassDetailTableView';
import TableView from '../../../../table/TableView';
import { PMDataVM } from '../../../single/form/PMDataVM';

const pmDataVM = () => PMDataVM.Instance;

class MassDetailView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassDetailVM();
    } else {
      this.state = store;
    }
  }

  componentDidMount() {
    var data = this.state;
    const id = this.props.match.params.id;
    data.id = id;

    // Set up Main Width
    pmDataVM().Main_Classname = 'massdetail__comp'
  }

  render() {
    var data = this.state;

    return (
      <>
        <h4 className='text-center'>BNG CHI TIT KHI LNG</h4>
        <div>
          <MassDetailTableView store={data.DetailTable} />
        </div>
      </>
    );
  }
}

export default withRouter(observer(MassDetailView));
